<template>
<dashboard-navbar @signout-click="signout"></dashboard-navbar>
<router-view v-if="situation"></router-view>
</template>

<script>
import dashboardNavbar from '@/components/DashboardNavbar.vue'
export default {
  components: {
    dashboardNavbar
  },
  data () {
    return {
      situation: false
    }
  },
  mounted () {
    this.check()
  },
  methods: {
    check () {
      const token = document.cookie.replace(/(?:(?:^|.*;\s*)hexToken\s*=\s*([^;]*).*$)|^.*$/, '$1')
      if (token) {
        this.$http.defaults.headers.common.Authorization = token
        this.$http.post(`${process.env.VUE_APP_API}/v2/api/user/check`, { token })
          .then(res => {
            console.log(res)
            this.situation = true
          })
          .catch(err => {
            console.dir(err)
            alert(err.data.message)
            this.$router.push('/signin')
          })
      } else {
        alert('您尚未登入')
        this.$router.push('/signin')
      }
    },
    signout () {
      document.cookie = 'hexToken=; expires=;'
      this.$router.push('/signin')
    }
  }
}
</script>
